

.about-main {
  display: flex;
  width: 100%;
  height: 90vh;
  
  background-color: rgb(6, 72, 83);
}
.about-right {
  display: flex;
 flex-direction: column;
  flex: 1;
  margin: 150px 70px;
}
.about-left {
  flex: 1;
 

  background-color: rgb(6, 72, 83);
  display: flex;align-items: center;justify-content: center;
}
.about-maincreator {
}
.about-name {
  border-bottom: 1px solid rgb(233, 228, 228);
  font-size: 55px;
  font-family: 'Courier Prime', monospace;
  color: white;
  width: 90%;
 
  font-weight: 500;
  margin-bottom: 10px;
}
.more-me {
  display: flex;align-items: center;
  margin: 30px;
  font-size: 30px;
  color: rgb(253, 250, 250);
  font-weight: 300;

  width: 90%;
  font-family: "Poppins", sans-serif;
  margin-bottom: 20px;
}
.about-description {
  margin: 30px;
  font-size: 25px;
  font-weight: 300;

  color: rgb(250, 248, 248);
  width: 90%;
  font-family: "Poppins", sans-serif;
}
.left-img-to{
  width: 384px;
  height: 360px;
  object-fit: cover;
  border-radius: 15px;
  margin-left: 26px;
  display: flex;align-items: center;justify-content: center;

}
.imghet{
  margin-top: 30px;
  display: flex;align-items: center;justify-content: center;
width: 360px;

height: 360px;

background-color: rgb(158, 150, 150);
border-radius: 2px;
}
.about-naverme{
margin-top: -490px;
margin-left: 450px;
position: absolute;
}



@media  screen and (max-width:490px) {
  .about-main{
    display: flex;flex-direction: column;align-items: center;height: auto;overflow: hidden;
  }
  .imghet{
   
    display: none;
  }
  .about-naverme{
    display: none;
  }
  .about-maincreator{
    display: flex;flex-direction: column;align-items: center;
  }
  .about-description,.more-me{
    width: 100%;
    font-size: 20px;
  }
  .about-name{
    width: 100%;
font-size: 30px;
  }
}

@media  screen and (max-width:809px) {
  .about-main{
    display: flex;flex-direction: column;align-items: center;height: auto;overflow: hidden;
  }
  .imghet{
   
    margin-top: -80px;
    margin-bottom:20px
  }
  .about-naverme{
    display: none;
  }
  .about-maincreator{
    display: flex;flex-direction: column;align-items: center;
  }
  
}
@media screen and (max-width:1200px){
  .about-naverme{
    display: none;
  }
  .about-main{
    display: flex;align-items: center;height: auto;overflow: hidden;
  }
}
